.navigator{
    display: flex;
    flex-direction: column;
    margin-left: 4%;

    font-family: Poppins;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 36px;
    /* identical to box height */
    // background-color: #000000;

    position: fixed;
    top: 40%;
    width: 10vw;
    z-index: 3;


    color: #000000;
    
    a{
        text-decoration: none;
        color: #000;
    }

    .navigator_push{
        margin-bottom: 25%;
    }
}

